.card {
  color: #060b35;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3ms;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  background-position: top;
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: transform .3ms;
  position: relative;
}

.card__title:hover {
  cursor: pointer;
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 4px 0 7px;
  padding: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews {
  text-align: right;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-weight: 400;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  transition: transform .3;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  line-height: 14px;
  display: flex;
}

.stars_star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars_star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 div:nth-child(-n), .stars--1 div:nth-child(-n+1), .stars--2 div:nth-child(-n+2), .stars--3 div:nth-child(-n+3), .stars--4 div:nth-child(-n+4), .stars--5 div:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  justify-content: space-between;
  margin: auto 0;
  padding: 0;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  margin: auto 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  text-decoration: none;
  transition: transform .3ms;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.logo {
  align-items: center;
  display: flex;
}

.catalog {
  --column-count: 1;
  grid-gap: 46px 48px;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}
